import './vendor';

var numOne = Math.floor(Math.random() * 10);
var numTwo = Math.floor(Math.random() * 10);

$(document).ready(function () {

    $('.megafon').on('click', function () {
        $('.tabs__item').removeClass('tabs__item--active');
        $('.tabs__item--megafon').addClass('tabs__item--active');
    });

    $('.mts').on('click', function () {
        $('.tabs__item').removeClass('tabs__item--active');
        $('.tabs__item--mts').addClass('tabs__item--active');
    });

    $('.tele2').on('click', function () {
        $('.tabs__item').removeClass('tabs__item--active');
        $('.tabs__item--tele2').addClass('tabs__item--active');
    });

    $('.beeline').on('click', function () {
        $('.tabs__item').removeClass('tabs__item--active');
        $('.tabs__item--beeline').addClass('tabs__item--active');
    });

    $(".input-phone").inputmask("+7 (999) 999-99-99", {
        placeholder: "_"
    });

    $('.capcha input').attr('placeholder', '' + numOne + ' + ' + numTwo + ' = ?');

    valid("#js-form");
    activeBtn('#js-form', $('#js-form'));
});



function valid(form) {
    $.validator.addMethod("minlenghtphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 10;
        },
        "");
    $.validator.addMethod("requiredphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 1;
        },
        "");
    $(form).validate({
        rules: {
            phone: {
                requiredphone: true,
                minlenghtphone: true
            },
            myTextarea: {
                required: true,
                minlength: 3
            },
            capcha: {
                required: true
            },
            password: {
                required: true,
                minlength: 6
            },
            password_confirmation: {
                minlength : 6,
                equalTo : ".password"
            }
        },
        messages: {
            name: {
                required: ''
            },
            phone: {
                required: ''
            },
            terms: {
                required: ''
            }
        },
        errorPlacement: function(error, element) {},
        highlight: function(element, errorClass, validClass) {
            $(element).addClass(errorClass).removeClass(validClass);
        },
        unhighlight: function(element, errorClass, validClass) {
            $(element).removeClass(errorClass).addClass(validClass);
        },
        submitHandler: function submitHandler(form) {
            //alert("Submitted!");
            $.ajax({
                type: $(form).attr('method'),
                url: $(form).attr('action'),
                data: $(form).serialize(),
                success: function (data) {
                    if(parseInt(data) == 1) {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    } else {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    }
                },
                error: function () {
                    $(form).removeClass('form--active');
                    $(form).siblings('.form__ok').addClass('form--active');
                }
            });

            return false;
        }
    });
}

function activeBtn(modal, forma) {
    var input = $('' + modal + ' input');
    var btn = $('' + modal + ' .btn');
    var res = numOne + numTwo;

    function proverka() {
        var valCapcha = $('.capcha input').val();
        console.log(valCapcha + " - " + res);
        if (forma.valid()) {
            if (valCapcha == res) {
                btn.removeClass('btn-disabled');
            } else {
                btn.addClass('btn-disabled');
            }
        } else {
            btn.addClass('btn-disabled');
        }
    }
    input.on('keyup', function () {
        proverka();
    });
    input.on('blur',function () {
        proverka();
    });
    input.on('click', function () {
        proverka();
    });
    input.one('change', function () {
        proverka();
    })
}